<script>
	import ContentHeader from '@/components/ContentHeader2'

	import { onMounted, ref } from 'vue'
	import axios from 'axios'
	import Swal from 'sweetalert2'
	import router from '@/router'
	import { useRoute } from 'vue-router'

	export default{
		components: {
			ContentHeader: ContentHeader
		},
		setup() {
			const route = useRoute()
			const percetakan_id = route.params.id
			const loading = ref(false)
			const percetakan = ref([])

			const getPercetakan = async () => {
				let { data } = await axios.get(`api/percetakan/getWhereId/${percetakan_id}`)
				percetakan.value = data
				percetakan.value.expired_date = percetakan.value.expired_date.slice(0, 10)
				console.log(percetakan.value)
			}

			const updateDataPercetakan = () => {
				loading.value = true
				axios.put(`api/percetakan/update/${percetakan_id}`, percetakan.value)
				.then((response) => {
					Swal.fire({
					  title: 'Berhasil!',
					  text: 'Berhasil mengedit data',
					  icon: 'success',
					  showConfirmButton: false,
					  timer: 1500
					})	
		    		console.log(response.data)
		    		router.push('/data-percetakan')
		    	})
		    	.catch((error) => {
		    		Swal.fire({
					  title: 'Gagal!',
					  text: 'Gagal mengedit data',
					  icon: 'error',
					  confirmButtonText: 'Ok'
					})	
		    		console.log(error)
		    	})	
			}

			onMounted(() => {
				getPercetakan()
			})

			return {
				percetakan, loading, updateDataPercetakan
			}
		}
	}

	
</script>